.router-row {
	height: 30px;
}

.router-cell {
	width: 30px;
}

.router-cell[data-lane-type="inlet"] {
	background-color: rgba(255, 255, 255, 0.5);
}

.router-cell[data-lane-type="outlet"] {
	background-color: rgba(0, 0, 0, 0.75);
}

th.router-cell {
	background-color: rgba(255, 255, 255, 0.5);
	font-family: sans-serif;
	text-align: center;
	font-size: 12px;
}

/* row headers */
th.router-cell[data-lane-type] {
	position: relative;
	text-align: right;
	padding: 8px;
	width: 80px;
}

th.router-cell[data-lane-type] button {
	border: none;
	background: none;
	color: white;
	white-space: nowrap;
	border: 1px solid white;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}


th.router-cell[data-lane-type="outlet"] {
	background-color: rgba(0, 0, 0, 0.5);
}

.router-cell .router-controlled-inlet {
	position: absolute;
	left: 8px;
	right: 8px;
	top: 8px;
	bottom: 8px;
}

.router-cell .router-controlled-inlet > * {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
}

.router-cell .router-controlled-inlet > div {
	pointer-events: none;
	z-index: 10;
}


.router-cell .router-controlled-inlet > input {
	width: 100%;
}


.router-cell:not([data-lane-type="outlet"]):nth-child(even) {
	background-color: rgba(255, 255, 255, 0.75);
}




